"use client"

import { useEffect, useMemo, useState } from "react"
import Link from "next/link"
import { useParams } from "next/navigation"
import { BookText, FileUp, AlertCircle } from "lucide-react"

import api from "@/lib/api"

import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Skeleton } from "@/components/ui/skeleton"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Separator } from "@/components/ui/separator"

interface Subject {
  id: string
  name: string
}

export default function SubjectWorkspacePage() {
  const { subjectId } = useParams<{ subjectId: string }>()
  const [subject, setSubject] = useState<Subject | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const title = useMemo(() => subject?.name ?? "Subject", [subject])

  useEffect(() => {
    async function fetchSubject() {
      try {
        setError(null)
        const res = await api.get<Subject>(`/subjects/${subjectId}`)
        setSubject(res.data)
      } catch (e: any) {
        const message = e?.response?.data?.message || "Failed to load subject"
        setError(String(message))
      } finally {
        setLoading(false)
      }
    }
    fetchSubject()
  }, [subjectId])

  return (
    <div className="space-y-4">
      <Breadcrumb>
        <BreadcrumbList>
          <BreadcrumbItem>
            <BreadcrumbLink href="/dashboard">Dashboard</BreadcrumbLink>
          </BreadcrumbItem>
          <BreadcrumbSeparator />
          <BreadcrumbItem>
            <BreadcrumbPage>{title}</BreadcrumbPage>
          </BreadcrumbItem>
        </BreadcrumbList>
      </Breadcrumb>

      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertTitle className="ml-2">Could not load subject</AlertTitle>
          <AlertDescription className="ml-6">{error}</AlertDescription>
        </Alert>
      )}

      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="rounded-md border bg-background p-2 text-foreground/80">
            <BookText className="h-5 w-5" />
          </div>
          <div>
            <h1 className="text-2xl font-semibold tracking-tight">
              {loading ? <Skeleton className="h-7 w-48" /> : title}
            </h1>
            <p className="text-muted-foreground text-sm">Your subject workspace</p>
          </div>
        </div>
        <div>
          <Button disabled>
            <FileUp className="mr-2 h-4 w-4" /> Upload document
          </Button>
        </div>
      </div>

      <Separator />

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2">
          <Skeleton className="h-48 w-full" />
          <Skeleton className="h-48 w-full" />
        </div>
      ) : (
        <Tabs defaultValue="overview" className="w-full">
          <TabsList>
            <TabsTrigger value="overview">Overview</TabsTrigger>
            <TabsTrigger value="documents">Documents</TabsTrigger>
            <TabsTrigger value="insights">Insights</TabsTrigger>
          </TabsList>
          <TabsContent value="overview" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>Progress</CardTitle>
                <CardDescription>Key metrics for this subject</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-sm text-muted-foreground">Coming soon</div>
              </CardContent>
            </Card>
          </TabsContent>
          <TabsContent value="documents" className="space-y-4">
            <Card className="border-dashed">
              <CardHeader>
                <CardTitle>No documents yet</CardTitle>
                <CardDescription>
                  Upload past exams, lecture notes, and PDFs to power your analysis.
                </CardDescription>
              </CardHeader>
            </Card>
          </TabsContent>
          <TabsContent value="insights" className="space-y-4">
            <Card className="border-dashed">
              <CardHeader>
                <CardTitle>Insights</CardTitle>
                <CardDescription>Topic heat maps and study guidance will appear here.</CardDescription>
              </CardHeader>
            </Card>
          </TabsContent>
        </Tabs>
      )}
    </div>
  )
}
